<template>
  <div>
    <h2 class="text-center mt-2">질문 등록</h2>
    <div>
      <table class="table table-bordered">
        <thead></thead>
        <tbody>
          <tr>
            <td>질문 내용</td>
            <td>
              <input type="text" id="question" v-model="qna.question" />
            </td>
          </tr>
          <tr>
            <td>작성자</td>
            <td>
              <input type="text" id="question" v-model="qna.userid" />
            </td>
          </tr>
          <tr>
            <td>답변</td>
            <td>
              <input type="text" id="answer" v-model="qna.answer" />
            </td>
          </tr>
          <tr>
            <td colspan="2">
              <div class="text-center">
                <a class="btn btn-primary" @click="createHandler">등록</a>
                <a class="btn btn-primary" @click="moveHandler">목록</a>
              </div>
            </td>
          </tr>
        </tbody>
      </table>
    </div>
  </div>
</template>

<script>
import http from "@/api/http";
export default {
  data() {
    return {
      qna: {},
    };
  },
  methods: {
    moveHandler() {
      this.$router.push({ name: "QnaList" });
    },
    createHandler() {
      //데이터 검증
      let err = false;

      let msg = "";

      !this.qna.question && ((msg = "질문을 입력해주세요"), (err = true));

      if (err) {
        alert(msg);
      } else {
        //등록 처리
        http
          .post("qna", this.qna)
          .then((response) => {
            if (response.data == "success") {
              alert("등록 성공");
              this.moveHandler();
            } else {
              alert("등록 실패");
            }
          })
          .catch((error) => {
            alert(error);
          });
      }
    },
  },
};
</script>

<style></style>
