<template>
  <div>
    <h2 class="text-center mt-2">SSAFY QnA 게시판</h2>
    <div class="row" style="align-items: center">
      <div class="col-2">
        <select class="form-control" v-model="key">
          <option value="all">--선택하세요</option>
          <option v-for="(k, index) in keys" :value="k" :key="index">
            {{ k }}
          </option>
        </select>
      </div>
      <div class="col-4">
        <input class="form-control" type="text" v-model="word" />
      </div>
      <button class="col-2 btn btn-primary" @click="getQna">검색</button>
    </div>
    <div v-if="qnas.length > 0">
      <table class="table">
        <colgroup>
          <col width="10%" />
          <col width="10%" />
          <col width="35%" />
          <col width="20%" />
          <col width="35%" />
        </colgroup>
        <thead>
          <tr>
            <th class="text-center">번호</th>
            <th class="text-center">글쓴이</th>
            <th class="text-center">질문</th>
            <th class="text-center">시간</th>
            <th class="text-center">수정/삭제</th>
          </tr>
        </thead>
        <tbody v-for="qna in qnas" :key="qna.qnano" @click="detail(qna.qnano)">
          <tr>
            <td v-text="qna.qnano" class="text-center align-middle"></td>
            <td v-text="qna.userid" class="text-center align-middle"></td>
            <td v-text="qna.question" class="text-center align-middle"></td>
            <td v-text="qna.regtime" class="text-center align-middle"></td>
            <td class="text-center align-middle">
              <input
                type="button"
                class="btn btn-primary"
                value="수정"
                @click="updateQna(qna.qnano)"
              />
              <input
                type="button"
                class="btn btn-danger"
                value="삭제"
                @click="deleteQna(qna.qnano)"
              />
            </td>
          </tr>
          <tr v-show="show == qna.qnano">
            <td colspan="5" v-text="qna.answer"></td>
          </tr>
        </tbody>
      </table>
    </div>
    <div v-else>등록된 QnA가 없습니다.</div>
  </div>
</template>

<script>
import http from "@/api/http";

export default {
  data() {
    return {
      qnas: [],
      word: "", //검색 단어
      keys: ["qnano", "question", "userid"], //option에 뿌려줄 데이터
      key: "all", //검색 조건,
      show: 0,
    };
  },
  created() {
    this.getQna();
  },
  methods: {
    getQna() {
      const url = `qna?key=${this.key}&pageNo=1&word=${this.word}`;
      // alert('검색..................' + url)
      http
        .get(url)
        .then((response) => {
          console.log(response);
          this.qnas = response.data;
        })
        .catch((error) => {
          alert(error);
        });
    },
    updateQna(qnano) {
      this.$router.push({ name: "QnaModify", query: { qnano: qnano } });
    },
    deleteQna(qnano) {
      http
        .delete(`qna/${qnano}`)
        .then((res) => {
          if (res.data == "success") {
            alert("삭제 성공");
            this.getQna();
          }
        })
        .catch((error) => {
          alert(error);
        });
    },
    detail(qnano) {
      if (this.show == qnano) {
        this.show = 0;
      } else this.show = qnano;
    },
  },
};
</script>

<style></style>
