<template>
  <div>
    <h2 class="text-center mt-2">SSAFY 도서 수정</h2>
    <div>
      <table class="table table-bordered">
        <thead></thead>
        <tr>
          <td>질문 내용</td>
          <td>
            <input type="text" id="question" v-model="qna.question" />
          </td>
        </tr>
        <tr>
          <td>답변</td>
          <td>
            <input type="text" id="answer" v-model="qna.answer" />
          </td>
        </tr>
        <tr>
          <td colspan="2">
            <div class="text-center">
              <button class="btn btn-primary" @click="modify">수정</button>
              <button class="btn btn-primary" @click="movePage">목록</button>
            </div>
          </td>
        </tr>
      </table>
    </div>
  </div>
</template>

<script>
import http from "@/api/http.js";
export default {
  data() {
    return {
      qnano: "",
      qna: {},
    };
  },
  created() {
    console.log(this.$route);
    this.qnano = this.$route.query.qnano;

    http
      .get(`qna/${this.qnano}`)
      .then((response) => {
        console.log(response.data);
        this.qna = response.data;
      })
      .catch((error) => {
        alert("데이터 조회 중 오류 발생");
        console.log(error);
      });
  },
  methods: {
    movePage() {
      this.$router.push({ name: "QnaList" });
    },
    modify() {
      let err = false;
      let msg = "";
      !this.qna.question && ((msg = "질문을 입력해주세요"), (err = true));

      if (err) {
        alert(msg);
      } else {
        //수정처리
        http
          .put(`qna/${this.qnano}`, this.qna)
          .then((response) => {
            if (response.data == "success") {
              alert("수정 성공");
              this.movePage();
            } else {
              alert("수정 실패");
            }
          })
          .catch((error) => {
            alert("수정 실패");
            console.log(error);
          });
      }
    },
  },
};
</script>

<style></style>
